import React from 'react';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';


function Projects() {
  return (
    <Grid item xs={12} container justify="center" alignItems="center" className="projects-container">
      <Grid className="projects-title" item container xs={11} alignItems="center" justify="flex-start">
        <Typography variant="body2">Freelance Projects</Typography>
      </Grid>
      <Grid xs={11} className="work-infoblock" item direction="column" onClick={() => console.log('hello world')}>
        <Typography variant="body2">Company landing page</Typography>
        <Divider style={{marginBottom: 15}} />
        <Typography variant="body1" gutterBottom>Project idea was to recreate new landing page on react framework</Typography>
        <Typography variant="body1" gutterBottom>Landing page priorities was to be able monitor user data and actions using Hotjar, Google Analytics and Google Optimize</Typography>
        <Typography variant="body1" gutterBottom>Google optimize was configured to work with React so Company could do realtime changes to software and the changes would show instantly</Typography>
        <Typography variant="body1" gutterBottom>Optimize was aswell configured to work with A/B testing, so customers would be able to see different variants of the page.</Typography>
      </Grid>
      <Grid xs={11} className="work-infoblock" item direction="column" onClick={() => console.log('hello world')}>
        <Typography variant="body2">Company compensation form</Typography>
        <Divider style={{marginBottom: 15}} />
        <Typography variant="body1" gutterBottom>Project idea was to create system where customer would fill up their compensation claims step by step</Typography>
        <Typography variant="body1" gutterBottom>Compensation claim collected customers input and stored the data to Companys dashboard after customer gave signature and accepted that the Company would be permitted to work with the compensation</Typography>
       </Grid>
       <Grid xs={11} className="work-infoblock" item direction="column" onClick={() => console.log('hello world')}>
        <Typography variant="body2">Company Dashboard</Typography>
        <Divider style={{marginBottom: 15}} />
        <Typography variant="body1" gutterBottom>Project idea was to create dashboard for ongoing compensation cases</Typography>
        <Typography variant="body1" gutterBottom>Company was using Trello to save all the cases and decided it would be better to have dashboard system to monitor and assign cases easily to employees</Typography>
       </Grid>
    </Grid>
  );
}

export default Projects;
