// @ts-nocheck
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

import React, { useRef, useState } from 'react';
import '../css/App.css'

import Basic from '../components/main/Basic';
import Experience from '../components/main/Experience';
import Projects from '../components/main/Projects';

import { Global, StyledContainer } from '../components/styles';

import { useTransition, useSpring, useChain, config } from 'react-spring'


export const data = [
  {
    css: 'linear-gradient(135deg, #a8edea 0%, #fed6e3 100%)',
    radius: 5,
    margin: 15,
    height: 200
  },
  {

    css: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
    height: 400
  },
  {

    css: 'linear-gradient(135deg, #e0c3fc 0%, #8ec5fc 100%)',
    height: 400
  },
];



function App() {

  const [open, set] = useState(false)

  const springRef = useRef()

  const { size, opacity, ...rest } = useSpring({
    ref: springRef,
    config: config.stiff,
    from: { size: '20%', background: '#DE354C', display: 'flex' },
    to: { size: open ? '100%' : '20%', background: open ? 'white' : '#DE354C', display: open ? 'grid' : 'flex', }
  })

  const transRef = useRef()

  const transitions = useTransition(open ? data : [], item => item.name, {
    ref: transRef,
    unique: true,
    trail: 400 / data.length,
    from: { opacity: 0, transform: 'scale(0)' },
    enter: { opacity: 1, transform: 'scale(1)' },
    leave: { opacity: 0, transform: 'scale(0)' }
  })

  useChain(open ? [springRef, transRef] : [transRef, springRef], [0, open ? 0.1 : 0.1])

  return (
    <>
    <Global />
      <StyledContainer style={{ ...rest, width: open ? size : '100%', height: size}} onClick={!open ? () => set(open => !open) : null}>
        {!open ? (
        <Typography className="app-title" component="">Tom</Typography>
        ) : (
          <Container maxWidth="sm">
          {transitions.map(({ item, key, props }, index) => (
            <Grid key={index} item xs={index === 0 ? 12 : 12}>
              {index === 0 && (<Basic key={index} {...item} />)}
              {index === 1 && (<Projects key={index} {...item} />)}
              {index === 2 && (<Experience key={index} {...item} />)}
            </Grid>
          ))}
          <Button style={{marginTop: 10, marginBottom: 10}} variant="outlined" fullWidth onClick={() => set(open => !open)}>Close</Button>
          </Container>
        )}
      </StyledContainer>
    </>
  )
}

export default App;